import styles from './Arrow.module.sass';

const Arrow = ({type='left'}) => {
    return (
        <>
            {type === 'left' ?
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                    width={20}
                    height={33}
                    fill="none"
                >
                    <path
                        fill="url(#a)"
                        fillOpacity={0.5}
                        d="M20 33h20v33H20z"
                        transform="rotate(180 20 33)"
                    />
                    <defs>
                        <pattern
                            id="a"
                            width={1}
                            height={1}
                            patternContentUnits="objectBoundingBox"
                        >
                            <use xlinkHref="#b" transform="matrix(.00518 0 0 .00314 0 -.002)"/>
                        </pattern>
                        <image
                            xlinkHref="data:image/png;base64,"
                            id="b"
                            width={193}
                            height={320}
                        />
                    </defs>
                </svg>
                :
                <svg
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink"
                    width={20}
                    height={33}
                    fill="none"
                >
                    <path fill="url(#a)" fillOpacity={0.5} d="M0 0h20v33H0z"/>
                    <defs>
                        <pattern
                            id="a"
                            width={1}
                            height={1}
                            patternContentUnits="objectBoundingBox"
                        >
                            <use xlinkHref="#b" transform="matrix(.00518 0 0 .00314 0 -.002)"/>
                        </pattern>
                        <image
                            xlinkHref="data:image/png;base64,"
                            id="b"
                            width={193}
                            height={320}
                        />
                    </defs>
                </svg>
            }
        </>
    );
}

    export default Arrow;